.zoomindimmer {
    position: fixed;
    background: rgba(0, 0, 0, 0.856);
    height: 100vh;
    width: 100%;
    z-index: 1;
    -webkit-backdrop-filter: grayscale(1);
    backdrop-filter: grayscale(1);
    display: none;
  }
  
  .zoomin {
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 1;
    display: none;
  }
  
  .zoomincontent {
    max-height: 100vh;
    max-width: 100vw;
    -webkit-animation: zoom 0.5s forwards var(--sleek);
    animation: zoom 0.5s forwards var(--sleek);
  }
  
  .zoomincontentAlign {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: zoom-out;
  }
  
  @-webkit-keyframes zoom {
    from {
      opacity: 0;
      transform: scale(0.95);
    }
  
    to {
      opacity: 1;
      transform: scale(1);
    }
  }
  
  @keyframes zoom {
    from {
      opacity: 0;
      transform: scale(0.95);
    }
  
    to {
      opacity: 1;
      transform: scale(1);
    }
  }